import "../../../css/dataTables.bootstrap4.css";
import React, { Component } from "react";
import axios from "axios";
import CIcon from '@coreui/icons-react'
import { cilPlus } from '@coreui/icons';
import {getToken} from '../../../Auth/common'
import {BASE_URL} from '../../../Auth/actions'

import {
    CButton,
    CCard,
    CCardBody,
    CCardHeader,
    CCol,
    CRow,
    CLink,
  } from '@coreui/react'

const $ = require("jquery");
$.Datatable = require("datatables.net");


export default class Index extends Component {
     constructor(props) {
         super(props);
         this.state = {
             branch: [],
             loading: true,
         };
     }

     // Keep as you have done
         // Keep as you have done
         async getUsersData() {
            this.mounted = true; 
            const URL = `${BASE_URL}/branch/list`; 
         
            const res = await axios(URL, {
                        method: "POST",     
                        headers: {              
                                "x-access-token":getToken(),
                                "Content-Type": "application/x-www-form-urlencoded",
                                "Accept": "application/json"                  
                        },
                    })
            this.setState({ loading: false, branch: res.data.results });
                        
        }
        
    // Init table data as component is mounted
    componentDidMount() {
        this.getUsersData().then(() => this.syncTable());
        // console.log('company',this.state.users)
    }
    
    // Split as new function to init the datatable
    syncTable() {
        this.$el = $(this.el);
        this.$el.DataTable({
        data: this.state.branch, //option 1
        // data: this.getUsersData1(), //option 2
        
    
        columns: [
            { title: "Record Status", data: "RECORD_STATUS" },
            { title: "Branch Code", data: "BRANCH_CODE" },
            { title: "Branch Name", data: "BRANCH_NAME" },
            { title: "Address", data: "ADDRESS" },
            { title: "Device ID", data: "DEVICE_ID" },
            { title: "Effective Date", data: "EFF_DATE" },
        ]
        
        });
        console.log('company',this.state.users)
    }

     componentWillMount() {}

     render() {
         return (
            <CRow>
                <CCol xs="12" md="12">
                    <CCard>
                        <CCardHeader>
                            <CLink to="/period-create">
                                <CButton type="button"  size="sm" color="success">
                                <CIcon content={cilPlus} /> Add Branch
                                </CButton>
                            </CLink>                                  
                        </CCardHeader>
                        <CCardBody>                          
                            <table className="table table-bordered " width="100%"
                                ref={(el) => (this.el = el)}>
                            </table>
                        </CCardBody>
                    </CCard>         
                </CCol>        
            </CRow>
         );
     }
}